.profile__image{
    border-radius: 50%;
    height: 120px;
    width: 120px;
    object-fit: cover;
    }
.profile__image:hover{
    background: rgb(0, 0, 0);
    opacity: 0.2;
    background-repeat: no-repeat;
    background-size: 24px;
    background-position: center;
    cursor: pointer;
}